import React, { Component } from "react";
import "../styles/Showcase.css";
import Show from "../components/Show";


export default class Showcase extends Component {
  render() {
    return (
      <div>
        <div className="showcaseheader">
          <div className="wrappershowcase">
            <span className="wordshowcase word1showcase">Projects</span>
            <span className="wordshowcase word2showcase">Showcase</span>
          </div>
        </div>
        <div className="show">
          <Show />
        </div>
      </div>
    );
  }
}
