import React, { Component } from "react";
import { Link } from "react-router-dom";

export default class Nav extends Component {
  render() {
    return (
      <div>
        <div className="nav">
          <ul>
          <li>
              <p>
                <Link
                  style={{ textDecoration: "none", color: "#fff" }}
                  to="/"
                >
                  Home
                </Link>
              </p>
            </li>
            <li>
              <p>
                <Link
                  style={{ textDecoration: "none", color: "#fff" }}
                  to="/Showcase"
                >
                  Showcase
                </Link>
              </p>
            </li>
          </ul>
        </div>
      </div>
    );
  }
}
