import React, { Component } from "react";
import Typography from "@material-ui/core/Typography";
import Grid from "@material-ui/core/Grid";
import me from "../assets/thomas.jpg";
import SkillBar from "react-skillbars";
import { Parallax } from "react-scroll-parallax";

const skills = [
  { type: "Html & Css", level: 95 },
  { type: "Javascript", level: 80 },
  { type: "UX Design", level: 90 },
  { type: "ReactJS", level: 65 },
  { type: "Angular", level: 30 },
  { type: "PHP", level: 70 }
];

const colors = {
  bar: "#9bc5de",
  title: {
    text: "#585858",
    background: "#fff"
  }
};

export default class Info extends Component {
  render() {
    return (
      <div className="about">
        <Parallax className="parbetween" x={[0, -5]}>
          <Grid container spacing={0} justify="center">
            <Grid className="me" item lg={6} md={12}>
              <img src={me} width="400" height="400" alt="img" />
            </Grid>
            <Grid className="me" item lg={6} md={12}>
              <Typography className="text" variant="h1">
                ABOUT ME
              </Typography>
              <Typography variant="body1" component="p">
                Hi there, my name is Thomas Gertenbach. I am a 19 years old
                student, living in the Netherlands. As you might guess, one of
                my hobbies is programming. I love making things out of nothing,
                and ending up with a beautiful product I can be proud of. <br/> <br/> I
                study Creative Media and game technologies at Hogeschool
                Rotterdam
              </Typography>
            </Grid>
          </Grid>
        </Parallax>
        <Parallax className="parbetween" x={[-5, 0]}>
          <Grid className="skill" container spacing={0}>
            <Grid className="skilltitle" item lg={6} md={12}>
              <Typography variant="h3" component="h3">
                SKILLS
              </Typography>
              <Typography variant="h1" component="h1">
                Here is an indication of my skillset
              </Typography>
            </Grid>
            <Grid className="skills" item lg={6} md={12}>
              <SkillBar
                animationDuration={2000}
                height={"35px"}
                skills={skills}
                colors={colors}
              />
            </Grid>
          </Grid>
        </Parallax>
      </div>
    );
  }
}
