import React from "react";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import desk from "../assets/desktop.svg";
import Grid from "@material-ui/core/Grid";
import { Parallax } from "react-scroll-parallax";
import { Link } from "react-router-dom";


export default class Projects extends React.Component {
  render() {
    return (
      <div className="projects">
        <Grid className="project" container spacing={5}>
          <Grid className="projecttitle" item lg={6} xs={12}>
            <Parallax x={[-7.5, 7.5]}>
              <Typography variant="h3" component="h3">
                PROJECTS
              </Typography>
              <Typography variant="body1" component="p">
                Have a look at my projects. They vary from school projects to work and my own.
              </Typography>
              <Button variant="outlined">
                <Link style={{ textDecoration: "none", color: "#fff" }} to="/Showcase">
                  Click here to see my projects!
                </Link>
              </Button>
            </Parallax>
          </Grid>
          <Grid item lg={6} xs={12}>
            <Parallax x={[7.5, -7.5]}>
              <img src={desk} alt="img" />
            </Parallax>
          </Grid>
        </Grid>
      </div>
    );
  }
}
