import React, { Component } from "react";
import coding from "../assets/coding.svg";
import api from "../assets/api.svg";
import css from "../assets/css.svg";
import develop from "../assets/develop.svg";
import uix from "../assets/uix.svg";
import browser from "../assets/browser.svg";
import { Parallax } from "react-scroll-parallax";
import Grid from "@material-ui/core/Grid";
import Button from "@material-ui/core/Button";

export default class HeaderHome extends Component {

  render() {
    return (
      <div className="header">
        <Grid container spacing={0}>
          <Grid className="headergraph" item xs={12}>
            <div className="headerimg">
              <Parallax y={[-500, 150]}>
                <img className="image-1" src={coding} alt="img" />
              </Parallax>
              <Parallax y={[-120, 90]}>
                <img className="image-2" src={api} alt="img" />
              </Parallax>
              <Parallax y={[-90, 90]}>
                <img className="image-3" src={develop} alt="img" />
              </Parallax>
              <Parallax y={[-80, 80]}>
                <img className="image-4" src={uix} alt="img" />
              </Parallax>
              <Parallax y={[-100, 100]}>
                <img className="image-5" src={css} alt="img" />
              </Parallax>
              <Parallax y={[-60, 60]}>
                <img className="image-6" src={browser} alt="img" />
              </Parallax>
            </div>

            <div className="wrapper">
              <span className="word word1">Thomas</span>
              <span className="word word2">Gertenbach</span>
              <span className="word word3">I design and build</span>
              <span className="word word3">user interfaces</span>
              <Button onClick={this.props.buttonClick} variant="outlined">
                Click here to get to know me!
              </Button>
            </div>
          </Grid>
        </Grid>
      </div>
    );
  }
}
